:root {
  --bg: #f4f2ee;
  --bg-2: #ece8df;
  --surface: #fdfcf9;
  --surface-2: #f2ede4;
  --ink: #1a1c1e;
  --ink-soft: #5b606b;
  --ink-faint: #8a8e99;
  --accent: #0f4c81;
  --accent-2: #b36b2d;
  --accent-3: #1b6b61;
  --line: #d8d1c1;
  --shadow-sm: 0 12px 32px rgba(26, 28, 30, 0.06);
  --shadow-lg: 0 40px 80px rgba(26, 28, 30, 0.1);
  --radius-lg: 20px;
  --radius-md: 14px;
  --radius-sm: 8px;
  --font-body: "Readex Pro", sans-serif;
  --font-display: "Changa", sans-serif;
  --font-mono: "IBM Plex Mono", monospace;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: var(--font-body);
  color: var(--ink);
  background: var(--bg);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
}
body::before {
  content: "";
  position: fixed;
  inset: 0;
  background: radial-gradient(circle at 8% 8%, rgba(15, 76, 129, 0.08), transparent 42%),
    radial-gradient(circle at 92% 12%, rgba(179, 107, 45, 0.08), transparent 42%),
    radial-gradient(circle at 50% 100%, rgba(27, 107, 97, 0.12), transparent 50%),
    linear-gradient(165deg, #f4f2ee 0%, #ece8df 100%);
  z-index: -2;
}
body::after {
  content: "";
  position: fixed;
  inset: 0;
  background-image: linear-gradient(var(--line) 0.5px, transparent 0.5px),
    linear-gradient(90deg, var(--line) 0.5px, transparent 0.5px);
  background-size: 80px 80px;
  opacity: 0.14;
  z-index: -1;
  pointer-events: none;
}
html[dir="rtl"] body {
  direction: rtl;
}
html[dir="rtl"] .flex-row {
  flex-direction: row-reverse;
}
.animate-fade-in {
  animation: fadeIn 0.6s ease forwards;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(16px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-pulse-soft {
  animation: pulseSoft 2s ease-in-out infinite;
}
@keyframes pulseSoft {
  0%, 100% {
    opacity: 0.4;
  }
  50% {
    opacity: 1;
  }
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: var(--bg-2);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb {
  background: var(--line);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--ink-faint);
}
:focus-visible {
  outline: 2px solid var(--accent);
  outline-offset: 2px;
}
